import { Component, OnInit, Input, Output, EventEmitter  } from '@angular/core';
import { interval } from 'rxjs';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-debugging-view',
  templateUrl: './debugging-view.component.html',
  styleUrls: ['./debugging-view.component.less']
})
export class DebuggingViewComponent implements OnInit {
  audio = new Audio('assets/music/click.mp3');
  settingTitle = "My IT program";
  accountTitle = "Account settings";
  networkTitle = "Network settings";
  deviceTitle = "Devices";
  checkTitle = "Scan";
  showError = "";
  errorTitle = "";
  errorNumber1 = "";
  errorNumber2 = "";

  @Input() showDEBProgram: string;
  @Output() closedProgramDEB = new EventEmitter<string>();
  @Output() errorResolved = new EventEmitter<boolean>();

  passwTitle = "Password";
  activeInactTitle = "Account status";
  bluetoothTitle = "bluetooth"
  wifiTitle = "wifi";
  lanTitle = "LAN cable";
  headsetTitle = "Headset";
  printerTitle = "Printer";
  monitor2Title = "Second Monitor";

  generatedErrors = false;
  accountVisible = false;
  networkVisible = false;
  devicesVisible = false;
  status1 = false;
  status2 = false;
  status3 = false;
  errToChange = 0;
  sts1opon1 = true;
  sts1opon2 = true;
  sts2opon1 = true;
  sts2opon2 = true;
  sts2opon3 = true;
  sts3opon1 = true;
  sts3opon2 = true;
  sts3opon3 = true;

  subscription: Subscription;
  public counterEvery = 1000;
  public secondsCounter = interval(this.counterEvery);

  constructor() {}
  ngOnInit() {}

  openSector(indexOfSector) {
    this.audio.play();
    if (this.generatedErrors === false) {
      this.generatedActiveErrors();
      this.generatedErrors = true;
    }

    if (indexOfSector === 1) {
      this.accountVisible = true;
    } else if (indexOfSector === 2) {
      this.networkVisible = true;
    } else {
      this.devicesVisible = true;
    }
  }

  isEverythingCompleted() {
    if (this.accountVisible == false || this.networkVisible == false || this.devicesVisible == false) {
      return false;
    }

    if (!this.sts1opon1) {
      return false;
    }
    if (!this.sts1opon2) {
      return false;
    }
    if (!this.sts2opon1) {
      return false;
    }
    if (!this.sts2opon2) {
      return false;
    }
    if (!this.sts2opon3) {
      return false;
    }
    if (!this.sts3opon1) {
      return false;
    }
    if (!this.sts3opon2) {
      return false;
    }
    if (!this.sts3opon3) {
      return false;
    }

    return true;
  }

  changeStatusErr() {
    this.showOrHideError(this.errToChange - 1, true);
  }

  showOrHideError(indexError, boolValue) {
    switch(indexError) {
      case 0:
        this.sts1opon1 = boolValue;
        break;

      case 1:
        this.sts1opon2 = boolValue;
        break;

      case 2:
        this.sts2opon1 = boolValue;
        break;

      case 3:
        this.sts2opon2 = boolValue;
        break;

      case 4:
        this.sts2opon3 = boolValue;
        break;

      case 5:
        this.sts3opon1 = boolValue;
        break;

      case 6:
        this.sts3opon2 = boolValue;
        break;

      case 7:
        this.sts3opon3 = boolValue;
        break;
    }
  }

  generatedRandomNumber(numberOfitems) {
    var randomIndx = Math.floor(Math.random() * (numberOfitems));
    return randomIndx;
  }

  generatedActiveErrors() {
    var randomNumberOfErrors = this.generatedRandomNumber(3);

    for (var i = 0; i < (randomNumberOfErrors + 1); i++) {
      var randomIndx = Math.floor(Math.random() * (8));
      this.showOrHideError(randomIndx, false);
    }
  }

  openDebugWindow(title, inputType, outputType) {
    this.audio.play();
    this.showError = "Open";
    this.errorTitle = title;
    this.errorNumber1 = inputType;
    this.errorNumber2 = outputType;
  }

  scanSector(sectorNumber) {
    this.audio.play();
    if (this.checkTitle === "Scan") {
      this.checkTitle = "Scanning";
      this.subscription = this.secondsCounter.subscribe(val => console.log(val));
      setTimeout(() => this.checkStatus(sectorNumber), 2000);
    }
  }

  checkStatus(idx) {
    if (idx === 1) {
      this.status1 = true;
    } else if (idx === 2) {
      this.status2 = true;
    } else {
      this.status3 = true;
    }
    this.subscription.unsubscribe();
    this.checkTitle = "Scan";
  }

  closeMyProgramWindow() {
    this.audio.play();
    if (this.isEverythingCompleted()) {
      var currentFame = parseInt(window.sessionStorage.getItem("fame"));
      if (currentFame != 100) {
        currentFame += 5;
        window.sessionStorage.setItem("fame", currentFame.toString());
      }
      this.errorResolved.emit(true);
    } else {
      this.errorResolved.emit(false);
    }

    this.status1 = false;
    this.status2 = false;
    this.status3 = false;
    this.sts1opon1 = true;
    this.sts1opon2 = true;
    this.sts2opon1 = true;
    this.sts2opon2 = true;
    this.sts2opon3 = true;
    this.sts3opon1 = true;
    this.sts3opon2 = true;
    this.sts3opon3 = true;
    this.accountVisible = false;
    this.networkVisible = false;
    this.devicesVisible = false;
    this.generatedErrors = false;

    this.showDEBProgram = "";
    this.closedProgramDEB.emit(this.showDEBProgram);
  }

  receivedClosedError($event) {
    this.showError = $event;
    this.changeStatusErr();
  }

  receivedError1($event) {
    this.errToChange = $event;
  }
}
