import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-computer-desktop-view',
  templateUrl: './computer-desktop-view.component.html',
  styleUrls: ['./computer-desktop-view.component.less']
})
export class ComputerDesktopViewComponent implements OnInit {
  @Input() showComputer: string;
  @Input() errorId: string;
  @Input() isItView: boolean;
  @Output() solvedError = new EventEmitter<string>();
  @Output() computerClosed = new EventEmitter<string>();

  audio = new Audio('assets/music/usb.mp3');
  audio2 = new Audio('assets/music/click.mp3');

  visibleMenu = false;
  usbConnected = false;
  showDEVProgram = '';
  showDEBProgram = '';
  myDate = new Date();

  constructor() {}

  ngOnInit() {}

  closeComputerWindow() {
    this.audio2.play();
    this.usbConnected = false;
    if (this.showDEVProgram != '' || this.showDEBProgram != '') {
      return
    }

    this.showComputer = '';
    this.computerClosed.emit(this.showComputer);
    this.closeMenu();
  }

  openMenu() {
    this.audio.play();
    this.usbConnected = true;
    this.visibleMenu = true;
  }

  closeMenu() {
    this.visibleMenu = false;
  }

  openMyProgram() {
    if (this.isItView) {
      this.showDEVProgram = "Open";
    } else {
      this.showDEBProgram = "Open";
    }
  }

  receivedClosedProgramDEV($event) {
    this.showDEVProgram = $event;
  }

  receivedClosedProgramDEB($event) {
    this.showDEBProgram = $event;
  }

  receivedErrStatus($event) {
    if ($event === true) {
      this.solvedError.emit(this.errorId);
    }
  }

  receivedCloseComputer($event) {
    if ($event == true) {
      this.showComputer = '';
      this.computerClosed.emit(this.showComputer);
      this.closeMenu();
    }
  }
}
