import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { interval } from 'rxjs';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-it-system-view',
  templateUrl: './it-system-view.component.html',
  styleUrls: ['./it-system-view.component.less']
})
export class ItSystemViewComponent implements OnInit {
  audio = new Audio('assets/music/click.mp3');
  @Input() showDEVProgram: string;
  @Output() closedProgramDEV = new EventEmitter<string>();
  @Output() closeComputer = new EventEmitter<boolean>();

  percentNumber;
  workPercentage = window.sessionStorage.getItem("work");
  storeErrors = parseInt(window.sessionStorage.getItem("errors"));
  bugNotify = false;
  messageText = "Hi I got some problems with my computer. Can you come here pls?"
  messagesArr = [
    "Hi I got some problems with my computer. Can you come here pls?",
    "Hi, my PC is broken. Can you come up here?",
    "Please help! My PC is not working. Hurry!!",
    "Hi I think i got some virus here. I need you here.",
  ];
  arrayOfPercent = [6, 9, 11];

  positionT = (this.generatedRandomNumber(90)).toString() + '%';
  positionL = (this.generatedRandomNumber(90)).toString() + '%'
  availableClock = true;
  showClock = '100';

  generateClock: Subscription;
  counterEvery = 1000;
  secondsCounter = interval(this.counterEvery);
  localStorageChecker: Subscription;

  constructor() {}

  ngOnInit() { }

  setLocalStorageChecker() {
    this.localStorageChecker = this.secondsCounter.subscribe(val => this.checkLocalStorage());
  }

  closeDevelopment() {
    this.audio.play();
    this.showDEVProgram = "";
    this.closeComputer.emit(false);
    this.closedProgramDEV.emit(this.showDEVProgram);
  }

  takeTime() {
    this.audio.play();
    this.setLocalStorageChecker();
    if (this.availableClock && !this.bugNotify) {
      this.percentNumber = parseInt(this.workPercentage);
      var number = this.generatedRandomNumber(3);
      this.percentNumber += this.arrayOfPercent[number];

      if (this.isItWorkDone(this.percentNumber)) {
        this.workPercentage = "100";
      } else {
        this.workPercentage = this.percentNumber;
      }

      this.showClock = '0';
      this.availableClock = false;
      this.generateNextClock();

      window.sessionStorage.setItem("work", this.workPercentage.toString());
    }
  }

  showNotification() {
    this.messageText = this.messagesArr[this.generatedRandomNumber(this.messagesArr.length)];
    this.bugNotify = true;
    this.localStorageChecker.unsubscribe();
  }

  acceptBug() {
    this.audio.play();
    this.bugNotify = false;
    this.showDEVProgram = "";
    this.closeComputer.emit(true);
    this.localStorageChecker.unsubscribe();
    this.closedProgramDEV.emit(this.showDEVProgram);
  }

  rejectBug() {
    this.audio.play();
    var currentFame = parseInt(window.sessionStorage.getItem("fame"));
    if (currentFame > 0) {
      currentFame -= 20;
    }

    window.sessionStorage.setItem("fame", currentFame.toString());
    this.bugNotify = false;
    this.localStorageChecker.unsubscribe();
  }

  //------------------------------

  isItWorkDone(progress) {
    if (progress >= 100) {
      return true;
    }

    return false;
  }

  checkLocalStorage() {
    var currentErrors = parseInt(window.sessionStorage.getItem("errors"));

    if (currentErrors !== this.storeErrors) {
      this.showNotification();
    }
  }

  generateNextClock() {
    this.generateClock = this.secondsCounter.subscribe();
    setTimeout(() => this.generateHiddenClock(), 5000);
  }

  generatedRandomNumber(numberOfitems) {
    var randomIndx = Math.floor(Math.random() * (numberOfitems));
    return randomIndx;
  }

  generateHiddenClock() {
    this.showClock = '100';
    this.availableClock = true;
    this.positionT = (this.generatedRandomNumber(90)).toString() + '%';
    this.positionL = (this.generatedRandomNumber(90)).toString() + '%';
    this.generateClock.unsubscribe();
  }
}
