import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { IntroViewComponent } from './intro-view/intro-view.component';
import { EngineViewComponent } from './engine-view/engine-view.component';
import { InfoViewComponent } from './info-view/info-view.component';

const routes: Routes = [
  {path: '', redirectTo: '/itguy', pathMatch: 'full'},
  {path: 'itguy', component: IntroViewComponent},
  {path: 'itguy/engineView', component: EngineViewComponent},
  {path: 'itguy/informationView', component: InfoViewComponent}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
export const routingComponents = [
  IntroViewComponent,
  EngineViewComponent,
  InfoViewComponent
]
