import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-game-over',
  templateUrl: './game-over.component.html',
  styleUrls: ['./game-over.component.less']
})
export class GameOverComponent implements OnInit {

  @Input() isGameOver: string;
  @Input() workPercentage: string;
  @Input() workDone: boolean;
  @Input() employeFame: string;
  @Input() bugsCatched: string;
  @Input() resultPoints: string;
  @Output() closedGameOver = new EventEmitter();

  resultTitle = "End of day";
  collectBtn = "Collect";
  graphBugs = "0";
  graphFame = "0";
  graphWork = "0";
  colWork = "";
  colFame = "";
  colBugs = "";
  showMyPoints = 0;
  audio = new Audio('assets/music/click.mp3');

  constructor() { }

  ngOnInit() {
  }

  checkLevel() {
    var lvl = window.sessionStorage.getItem("level");

    if (lvl == "1") {
      return 2;
    } else if (lvl == "2") {
      return 3;
    } else if (lvl == "3") {
      return 4;
    } else {
      return 1;
    }
  }

  replayThisLvl() {
    var level = this.checkLevel();

    if (level == 1) {
      window.sessionStorage.setItem("level", "0");
    } else if (level == 2) {
      window.sessionStorage.setItem("level", "1");
    } else if (level == 3) {
      window.sessionStorage.setItem("level", "2");
    } else if (level == 4) {
      window.sessionStorage.setItem("level", "3");
    } else {
      window.sessionStorage.setItem("level", "4");
    }
  }

  newLvl() {
    var level = this.checkLevel();

    if (level == 1) {
      window.sessionStorage.setItem("level", "1");
    } else if (level == 2) {
      window.sessionStorage.setItem("level", "2");
    } else if (level == 3) {
      window.sessionStorage.setItem("level", "3");
    } else {
      window.sessionStorage.setItem("level", "4");
    }
  }

  setNewGame(nextLvl) {
    if (nextLvl) {
      this.newLvl();
    } else {
      this.replayThisLvl();
    }

    this.colWork = "";
    this.colFame = "";
    this.colBugs = "";
    this.graphBugs = "0";
    this.graphFame = "0";
    this.graphWork = "0";

    this.closedGameOver.emit(false);
  }

  collectPoints(e) {
    this.audio.play();
    if (e == 0) {
      var halfPoints = parseInt(this.workPercentage) / 2;
      this.graphWork = String(halfPoints) + "%";
      this.colWork = "disabled";
      this.showMyPoints += 1;
    } else if (e == 1) {
      var halfPoints = parseInt(this.employeFame) / 2;
      this.graphFame = String(halfPoints) + "%";
      this.colFame = "disabled";
      this.showMyPoints += 1;
    } else {
      var halfPoints = parseInt(this.bugsCatched) / 2;
      this.graphBugs = String(halfPoints) + "%";
      this.colBugs = "disabled";
      this.showMyPoints += 1;
    }

    if (this.showMyPoints === 3) {
      var result = parseInt(this.workPercentage) + parseInt(this.employeFame) + parseInt(this.bugsCatched);
      this.resultPoints = String(result);
    }
  }

  nextLevel() {
    this.audio.play();
    this.setNewGame(true);
  }

  replayLevel() {
    this.audio.play();
    this.setNewGame(false);
  }
}
