import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

@Component({
  selector: 'app-person',
  template: `
    <div class="person" [@moveOnFloor]="isMoved">
      <div class="head"></div>
      <div class="body"></div>
    </div>
  `,
  styleUrls: ['./person-style.less'],
  animations: [
    trigger('moveOnFloor', [
      state('moved', style({ right: '-20%' })),
      state('notmoved', style({ right: '35%' })),
      transition('notmoved => moved', animate("0.5s")),
      transition('moved => notmoved', animate("0.5s 1.8s"))
    ])
  ]
})
export class PersonComponent implements OnInit {
  public alreadyMoved = '';

  @Input() isMoved: string;
  @Output() personMovedToElevvator = new EventEmitter<string>();

  constructor() { }

  ngOnInit() {
    console.log("Person", this.isMoved);
  }

}
